import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import React from 'react'
import { desktopVh, desktopVw } from '../../styles/utils'
import ScaledImage from '../ScaledImage'
import Button from '../Button'
import { getLinkHref, getLinkPath, getStyledPrice } from '../../utils'
import TrackableLink from '../TrackableLink'

const HeroSection = styled.div`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(143)} ${desktopVw(55)} ${desktopVw(88)}
      ${desktopVw(74)};
    background: ${props => props.backgroundColor};
    margin-top: -${desktopVw(79)};
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
`
const LeftWrapper = styled.div`
  width: ${desktopVw(350)};
`
const Title = styled.div`
  h1 {
    font-family: ${({ theme }) => theme.fonts.secondary};
    font-size: ${desktopVw(50)};
    font-weight: 500;
    margin-bottom: ${desktopVw(54)};
  }
  p {
    font-size: ${desktopVw(18)};
  }
`
const StyledButton = styled(Button)`
  font-size: ${desktopVw(16)};
  font-weight: 500;
  width: ${desktopVw(290)};
  padding: ${desktopVw(31)} 0;
  text-transform: uppercase;
  margin-top: ${desktopVw(44)};
  background: ${({ theme }) => theme.colors.primary};
  color: ${({ theme }) => theme.colors.secondary};
`
const RightWrapper = styled.div``
const BannerSection = styled.div`
  display: grid;
  grid-template-areas:
    'hero-image__1 hero-image__2 hero-image__3'
    'hero-image__1 hero-image__4 hero-image__5';
  grid-template-rows: ${desktopVw(259)} ${desktopVw(259)};
  grid-template-columns: ${desktopVw(427)} ${desktopVw(207)} ${desktopVw(207)};
  grid-gap: ${desktopVw(21)};
`
const TextWrapper = styled.div`
  position: absolute;
  margin: ${desktopVw(14)};
  display: flex;
  justify-content: space-between;
  font-size: ${props => props.fontSize};
  bottom: 0;
  color: ${({ theme }) => theme.colors.secondary};
  width: calc(100% - 2vw);
  opacity: 0;
  transition: opacity 250ms;
  sup {
    font-size: ${desktopVw(11)};
  }
`
const StyledImage = styled(ScaledImage)``
const HoverCard = styled.div`
  position: relative;
  cursor: pointer;
  &:hover {
    ${TextWrapper} {
      opacity: 1;
    }
    ${StyledImage} {
      filter: brightness(0.6);
    }
  }
`

export function Desktop({ desktopBanners, sectionContent }) {
  return (
    <HeroSection backgroundColor={sectionContent?.backgroundColor?.hexcode}>
      <LeftWrapper>
        <Title>{documentToReactComponents(sectionContent?.title.json)}</Title>
        <StyledButton
          to={
            sectionContent?.button.url
              ? undefined
              : getLinkPath(sectionContent?.button)
          }
          href={getLinkHref(sectionContent?.button)}
          target='_blank'
          trackParams={{
            trackGA: true,
            trackInternal: true,
            category: 'navigation',
            action: 'click_hero_card',
            label: sectionContent?.button.text,
            value: sectionContent?.button.url
              ? getLinkHref(sectionContent?.button)
              : getLinkPath(sectionContent?.button),
          }}
        >
          {sectionContent?.button.text}
        </StyledButton>
      </LeftWrapper>
      <RightWrapper>
        <BannerSection>
          {desktopBanners
            .slice(0, 5)
            .map(
              ({ imageDesktop, title, price, subtitle, link, id }, index) => (
                <HoverCard
                  key={id}
                  style={{ gridArea: `hero-image__${index + 1}` }}
                >
                  <TrackableLink
                    to={getLinkPath(link)}
                    trackParams={{
                      trackGA: true,
                      trackInternal: true,
                      category: 'navigation',
                      action: 'click_hero_card',
                      label: link.text,
                      value: getLinkPath(link),
                    }}
                  >
                    <StyledImage
                      alt={imageDesktop.description || imageDesktop.title}
                      loading='eager'
                      fluid={imageDesktop.fluid}
                    />
                    <TextWrapper
                      fontSize={index === 0 ? desktopVw(18) : desktopVw(10)}
                    >
                      <p>{title?.title}</p>
                      <p style={{ textAlign: 'right' }}>
                        {subtitle ?? ''} ${Number(price).toFixed(2)}
                      </p>
                    </TextWrapper>
                  </TrackableLink>
                </HoverCard>
              ),
            )}
        </BannerSection>
      </RightWrapper>
    </HeroSection>
  )
}
