import React, { useEffect } from 'react'
import styled from 'styled-components'
import Image from 'gatsby-image'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'

import { desktopVw, mobileVw } from '../styles/utils'
import { imageSources } from '../utils'

import useFetch from '../hooks/useFetch'

import ReviewsCarousel from './ReviewsCarousel'
import ReviewsList from './ReviewsList'
import SpacerImg from './SpacerImg'
import useDetectDevices from '../hooks/useDetectDevices'
import { InstagramHandle } from './common/InstagramHandle'
import useMounted from '../hooks/useMounted'

const StyledReviewSection = styled.section`
  display: flex;
  flex-direction: column;
  margin: 20px 0;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: row;
    margin: ${desktopVw(80)} 0;
    height: ${desktopVw(950)};
  }
`

const Title = styled.span`
  font-family: ${props => props.theme.fonts.secondary};
  font-weight: 500;
  line-height: 40px;
  white-space: pre-wrap;
  text-transform: uppercase;
  color: ${props => props.color || props.theme.colors.secondary};
  font-size: ${mobileVw(45)};
  line-height: ${mobileVw(40)};

  h1 {
    font-family: ${props => props.theme.fonts.heading};
  }

  p {
    font-family: ${props => props.theme.fonts.secondary};
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(90)};
    line-height: ${desktopVw(65)};

    h1 {
      font-size: ${desktopVw(90)};
      line-height: ${desktopVw(78.5)};
    }

    p {
      font-size: ${desktopVw(60)};
      line-height: ${desktopVw(65)};
    }
  }
`

const Loading = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 20px 0;
`

const StyledReviewsCarousel = styled(ReviewsCarousel)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const StyledReviewsList = styled(ReviewsList)`
  display: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
  }
`

const StyledImage = styled(Image)`
  opacity: 1;
`

const StyledInstagramHandleDesktop = styled(InstagramHandle)`
  opacity: 0;
`

const StyledInstagramHandleMobile = styled(InstagramHandle)`
  margin-top: 5px;
`

const ImageWrapper = styled.div`
  position: relative;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  box-sizing: border-box;
  height: 460px;
  padding: 0px ${mobileVw(20)};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: 50%;
    height: 100%;
    padding: ${desktopVw(20)} ${desktopVw(62)} ${desktopVw(40)};
    cursor: ${({ hasIgHandle }) => (hasIgHandle ? 'pointer' : 'initial')};
    &:hover ${StyledImage} {
      opacity: ${({ hasIgHandle }) => (hasIgHandle ? 0.5 : 1)};
    }
    &:hover ${Title} {
      opacity: ${({ hasIgHandle }) => (hasIgHandle ? 0.5 : 1)};
    }
    &:hover ${StyledInstagramHandleDesktop} {
      opacity: 1;
    }
  }
`

const fetchReviewsUtil = (fetchData, url) => {
  const headers = new Headers()
  headers.append('Content-Type', 'application/x-www-form-urlencoded')
  headers.append(
    'Authorization',
    process.env.GATSBY_STAMPED_AUTHORIZATION_TOKEN,
  )

  const options = {
    method: 'GET',
    headers,
    redirect: 'follow',
  }

  fetchData(url, options)
}

const ReviewsSection = React.forwardRef(
  (
    {
      text,
      image,
      imageDesktop,
      products,
      tracker,
      textColor,
      instagramHandle,
      productReviews,
    },
    ref,
  ) => {
    const { fetchData, response } = useFetch()
    const { devices } = useDetectDevices()
    const { mounted } = useMounted()

    useEffect(() => {
      let url = process.env.GATSBY_STAMPED_GET_REVIEWS_ENDPOINT.replace(
        'isWithPhotos',
        'isWithPhotos=true',
      )
      url = url.replace('minRating', 'minRating=5')

      fetchReviewsUtil(fetchData, url)
    }, [])

    return (
      <StyledReviewSection ref={ref}>
        <ImageWrapper
          hasIgHandle={!!instagramHandle}
          onClick={() => {
            if (instagramHandle && devices.desktop) {
              window.open(
                `https://www.instagram.com/${instagramHandle}/`,
                '_blank',
              )
            }
          }}
        >
          <StyledImage
            fluid={imageSources(image, imageDesktop)}
            style={{
              position: 'absolute',
              left: '0',
              right: '0',
              bottom: '0',
              top: '0',
              zIndex: '-1',
            }}
            alt={image.description || image.title}
          />
          {instagramHandle && devices.desktop && mounted && (
            <StyledInstagramHandleDesktop instagramHandle={instagramHandle} />
          )}
          <h3>
            <SpacerImg altText='Reviews' />
          </h3>
          <Title color={textColor ? textColor.toLowerCase() : null}>
            {documentToReactComponents(text)}
          </Title>
        </ImageWrapper>
        {devices.mobile && instagramHandle && mounted && (
          <StyledInstagramHandleMobile instagramHandle={instagramHandle} />
        )}
        <>
          {!response ? (
            <Loading>loading...</Loading>
          ) : response.data ? (
            <>
              <StyledReviewsCarousel reviews={productReviews} />
              <StyledReviewsList
                reviews={productReviews}
                tracker={tracker ? `${tracker}__reviews_section` : undefined}
              />
            </>
          ) : (
            <Loading>{response.message}</Loading>
          )}
        </>
      </StyledReviewSection>
    )
  },
)

export default ReviewsSection
