import React from 'react'
import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'

import { desktopVw } from '../../styles/utils'
import theme from '../../styles/theme'

import ScaledImage from '../ScaledImage'
import Button from '../Button'
import { getLinkPath, imageSources } from '../../utils'
import Video from '../Video'

const StyledDefault = styled.section`
  margin: 0 0 70px;
  position: relative;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    flex-direction: ${props =>
      props.reverse ? 'row-reverse !important' : null};
    margin: ${desktopVw(57)} 0 ${desktopVw(63)};
  }
`

const ImageWrapper = styled.div`
  position: relative;
  box-sizing: border-box;
  margin: 0 0 18px;
  height: 460px;
  width: 100%;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-shrink: 0;
    height: ${desktopVw(783)};
    width: ${desktopVw(560)};
    margin: 0 ${desktopVw(80)} 0 ${desktopVw(160)};
  }
`

const TextWrapper = styled.div`
  padding: 0 ${props => props.theme.spacings.mobile}px;

  h1,
  h2,
  h3 {
    font-size: ${props => (props.titleFontSize ? props.titleFontSize : '40')}px;
    line-height: 1.125;
    margin-bottom: 16px;
  }

  h4 {
    margin-bottom: 11px;
    font-family: ${props => props.theme.fonts.primary};
    font-size: 16px;
    line-height: 20px;
    font-weight: 400;
  }

  p {
    margin-bottom: 38px;
    font-size: ${props =>
      props.paragraphFontSize ? props.paragraphFontSize : '14'}px;
    line-height: 1.5;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    align-self: center;
    margin-top: ${desktopVw(53)};
    padding: 0;
    max-width: ${desktopVw(402)};

    h1,
    h2,
    h3 {
      margin-bottom: ${desktopVw(40)};
      font-size: ${props =>
        props.titleFontSizeDesktop
          ? desktopVw(props.titleFontSizeDesktop)
          : desktopVw(65)};
      line-height: 0.923;
    }

    h4 {
      margin-bottom: ${desktopVw(24)};
      font-size: ${desktopVw(25)};
      line-height: ${desktopVw(30)};
    }

    p {
      margin-bottom: ${desktopVw(40)};
      font-size: ${props =>
        props.paragraphFontSizeDesktop
          ? desktopVw(props.paragraphFontSizeDesktop)
          : desktopVw(20)};
      line-height: 1.25;
    }
  }
`

const StyledButton = styled(Button)`
  display: block;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: inline-block;
    align-self: flex-start;
    text-transform: uppercase;
    padding: ${desktopVw(25)} ${desktopVw(97)};
    font-size: ${desktopVw(18)};
  }
`

const Default = ({
  className,
  image,
  imageDesktop,
  text,
  button,
  reverse,
  titleFontSize,
  titleFontSizeDesktop,
  paragraphFontSize,
  paragraphFontSizeDesktop,
  video,
}) => (
  <StyledDefault
    className={`${className ? className : ''} default-layout ${
      reverse ? 'default-layout--reverse' : ''
    }`}
    reverse={reverse}
  >
    <ImageWrapper className='default-layout__image-wrapper'>
      {video ? (
        <Video
          key={video.file.url}
          url={video.file.url}
          poster={image.fluid.src}
          contentType={video.file.contentType}
          rootTopMargin={
            typeof window !== 'undefined'
              ? `${parseFloat(window.innerHeight * 1.5)}px`
              : '800px'
          }
        />
      ) : (
        <ScaledImage
          backgroundColor={theme.colors.greyLighter}
          fluid={imageSources(image, imageDesktop)}
          style={{
            position: 'absolute',
            left: '0',
            right: '0',
            bottom: '0',
            top: '0',
            zIndex: '-1',
          }}
          alt={image.description || image.title}
        />
      )}
    </ImageWrapper>
    <TextWrapper
      className='default-layout__text-wrapper'
      titleFontSize={titleFontSize}
      titleFontSizeDesktop={titleFontSizeDesktop}
      paragraphFontSize={paragraphFontSize}
      paragraphFontSizeDesktop={paragraphFontSizeDesktop}
    >
      {documentToReactComponents(text.json)}
      {button && (
        <StyledButton
          to={getLinkPath(button)}
          trackParams={{
            trackGA: true,
            trackInternal: true,
            category: 'home_page',
            action: 'click_layout_one_button',
            label: button.text,
            value: getLinkPath(button),
          }}
        >
          {button.text}
        </StyledButton>
      )}
    </TextWrapper>
  </StyledDefault>
)

export default Default
