import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import React from 'react'
import { mobileVh, mobileVw } from '../../styles/utils'
import Button from '../Button'
import { chunk } from '../../utils/build'
import TrackableLink from '../TrackableLink'
import { getLinkHref, getLinkPath } from '../../utils'
import ScaledImage from '../ScaledImage'

const Container = styled.div`
  margin-top: -59px;
  padding: 59px 0 23px 0;
  background: ${props => props.backgroundColor};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`
const ImageRowOverlapped = styled.div`
  display: flex;
  &:nth-child(1) {
    a {
      &:nth-child(1) {
        width: ${mobileVw(146)};
        height: ${mobileVw(183)};
        margin-right: ${mobileVw(16)};
      }
      &:nth-child(2) {
        width: ${mobileVw(148)};
        height: ${mobileVw(185)};
        margin-right: ${mobileVw(12)};
        margin-top: ${mobileVw(30)};
      }
      &:nth-child(3) {
        width: ${mobileVw(53)};
        height: ${mobileVw(183)};
        margin-top: ${mobileVw(7)};
      }
    }
  }
  &:nth-child(2) {
    a {
      &:nth-child(1) {
        width: ${mobileVw(121)};
        height: ${mobileVw(212)};
        margin-top: ${mobileVw(-20)};
        margin-right: ${mobileVw(17)};
      }
      &:nth-child(2) {
        width: ${mobileVw(133)};
        height: ${mobileVw(166)};
        margin-top: ${mobileVw(13)};
        margin-right: ${mobileVw(17)};
      }
      &:nth-child(3) {
        width: ${mobileVw(87)};
        height: ${mobileVw(167)};
        margin-top: ${mobileVw(31)};
      }
    }
  }
`
const ImageWrapper = styled.div``
const TextWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-top: 10px;
`
const Title = styled.div`
  h1 {
    font-family: ${({ theme }) => theme.fonts.secondary};
    font-size: ${mobileVw(30)};
    font-weight: 500;
    margin-bottom: 10px;
  }
  p {
    font-size: 10px;
    max-width: ${mobileVw(242)};
  }
`
const StyledButton = styled(Button)`
  font-size: 10px;
  font-weight: 500;
  width: ${mobileVw(232)};
  padding: 12px 0;
  text-transform: uppercase;
  margin-top: 10px;
  background: ${({ theme }) => theme.colors.primary};
  color: ${({ theme }) => theme.colors.secondary};
`

export function Mobile({ mobileBanners, sectionContent }) {
  return (
    <Container backgroundColor={sectionContent?.backgroundColor?.hexcode}>
      <ImageWrapper>
        {chunk(mobileBanners.slice(0, 6), 3).map(mobileBannerChunk => (
          <ImageRowOverlapped>
            {mobileBannerChunk.map(({ id, image, link }) => (
              <TrackableLink
                to={getLinkPath(link)}
                trackParams={{
                  trackGA: true,
                  trackInternal: true,
                  category: 'navigation',
                  action: 'click_hero_card',
                  label: link.text,
                  value: getLinkPath(link),
                }}
              >
                <ScaledImage
                  key={id}
                  alt={image.description || image.title}
                  loading='eager'
                  fluid={image.fluid}
                />
              </TrackableLink>
            ))}
          </ImageRowOverlapped>
        ))}
      </ImageWrapper>
      <TextWrapper>
        <Title>{documentToReactComponents(sectionContent?.title.json)}</Title>
        <StyledButton
          to={
            sectionContent?.button.url
              ? undefined
              : getLinkPath(sectionContent?.button)
          }
          href={getLinkHref(sectionContent?.button)}
          target='_blank'
          trackParams={{
            trackGA: true,
            trackInternal: true,
            category: 'navigation',
            action: 'click_hero_card',
            label: sectionContent?.button.text,
            value: sectionContent?.button.url
              ? getLinkHref(sectionContent?.button)
              : getLinkPath(sectionContent?.button),
          }}
        >
          {sectionContent?.button.text}
        </StyledButton>
      </TextWrapper>
    </Container>
  )
}
