import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../styles/utils'

import { getLinkPath } from '../utils'

import CategoryThumbnail from './CategoryThumbnail'
import TextButton from './TextButton'
import SpacerImg from './SpacerImg'

const StyledCategorySection = styled.div`
  padding: 0 10px;
  margin: 10px 0 67px;
  white-space: nowrap;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(62)} 0 ${desktopVw(57)};
    padding: 0 ${desktopVw(80)};
  }
`

const Collections = styled.section`
  display: flex;
  flex-flow: wrap;
  justify-content: space-between;
  margin-bottom: 51px;

  .category-thumbnail:nth-child(3n) {
    width: 100%;
    height: 182px;
  }

  .category-thumbnail:last-child {
    margin-bottom: 0;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(48)};

    .category-thumbnail:nth-child(3n) {
      width: calc(1 / 3 * 100% - (1 / 3 * ${desktopVw(80)}));
      height: ${desktopVw(573)};
    }
  }
`

const Categories = styled.section`
  display: flex;
  flex-direction: column;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
`

const CategoriesTitle = styled.h2`
  margin-bottom: 26px;
  color: ${props => props.theme.colors.greyLight};
  font-size: 15px;
  font-family: ${props => props.theme.fonts.primary};
  text-align: center;
  text-transform: uppercase;
  font-weight: normal;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 ${desktopVw(49)} 0 0;
    text-transform: initial;
    font-size: ${desktopVw(20)};
  }
`

const CategoryName = styled.h3`
  font-family: ${props => props.theme.fonts.primary};
  font-weight: normal;
`

const StyledTextButton = styled(TextButton)`
  font-size: 20px;
  text-align: center;
  margin-bottom: 29px;

  &:last-child {
    margin: 0;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 ${desktopVw(71)} 0 0;
    font-size: ${desktopVw(20)};
  }
`

const CategorySection = ({ collections, categoriesTitle, categories }) => {
  return (
    <StyledCategorySection>
      <Collections>
        {/* The following is required for SEO section heading but was affecting the layout of the images in mobile view */}
        {/* <h2><SpacerImg altText='Shop by collection' /></h2> */}
        {collections.map(({ id, images, link }, i) => (
          <CategoryThumbnail
            key={id}
            className='category-thumbnail'
            images={images}
            link={link}
            timeoutDelay={i * 1000}
          />
        ))}
      </Collections>
      <Categories>
        <CategoriesTitle>{categoriesTitle}</CategoriesTitle>
        {categories.map(link => (
          <StyledTextButton
            key={link.id}
            to={getLinkPath(link)}
            trackParams={{
              trackGA: true,
              trackInternal: true,
              category: 'home_page',
              action: 'click_shop_category',
              label: link.text,
              value: getLinkPath(link),
            }}
          >
            <CategoryName>{link.text}</CategoryName>
          </StyledTextButton>
        ))}
      </Categories>
    </StyledCategorySection>
  )
}

export default CategorySection
