import React from 'react'

import Default from './Default'
import TextOverlap from './TextOverlap'
import Tile from './Tile'

const ImageWithTextSection = ({
  className,
  image,
  imageDesktop,
  text,
  textMobile,
  button,
  layout,
  titleFontSize,
  titleFontSizeDesktop,
  paragraphFontSize,
  paragraphFontSizeDesktop,
  children,
  video,
}) => {
  const renderLayout = () => {
    switch (layout) {
      case 'Default': {
        return (
          <Default
            className={className}
            image={image}
            imageDesktop={imageDesktop}
            text={text}
            titleFontSize={titleFontSize}
            titleFontSizeDesktop={titleFontSizeDesktop}
            paragraphFontSize={paragraphFontSize}
            paragraphFontSizeDesktop={paragraphFontSizeDesktop}
            button={button}
            video={video}
          />
        )
      }

      case 'Default reverse': {
        return (
          <Default
            className={className}
            image={image}
            imageDesktop={imageDesktop}
            text={text}
            titleFontSize={titleFontSize}
            titleFontSizeDesktop={titleFontSizeDesktop}
            paragraphFontSize={paragraphFontSize}
            paragraphFontSizeDesktop={paragraphFontSizeDesktop}
            button={button}
            reverse
          />
        )
      }

      case 'Text overlap': {
        return (
          <TextOverlap
            className={className}
            image={image}
            imageDesktop={imageDesktop}
            text={text}
            titleFontSize={titleFontSize}
            titleFontSizeDesktop={titleFontSizeDesktop}
            paragraphFontSize={paragraphFontSize}
            paragraphFontSizeDesktop={paragraphFontSizeDesktop}
            button={button}
          />
        )
      }

      case 'Tile': {
        return (
          <Tile
            className={className}
            text={text}
            textMobile={textMobile}
            image={image}
            imageDesktop={imageDesktop}
            titleFontSize={titleFontSize}
            titleFontSizeDesktop={titleFontSizeDesktop}
            paragraphFontSize={paragraphFontSize}
            paragraphFontSizeDesktop={paragraphFontSizeDesktop}
            button={button}
          >
            {children}
          </Tile>
        )
      }

      case 'Tile reverse': {
        return (
          <Tile
            className={className}
            text={text}
            textMobile={textMobile}
            image={image}
            imageDesktop={imageDesktop}
            titleFontSize={titleFontSize}
            titleFontSizeDesktop={titleFontSizeDesktop}
            paragraphFontSize={paragraphFontSize}
            paragraphFontSizeDesktop={paragraphFontSizeDesktop}
            reverse
            button={button}
          />
        )
      }
      default: {
        return (
          <Default
            className={className}
            image={image}
            imageDesktop={imageDesktop}
            text={text}
            titleFontSize={titleFontSize}
            titleFontSizeDesktop={titleFontSizeDesktop}
            paragraphFontSize={paragraphFontSize}
            paragraphFontSizeDesktop={paragraphFontSizeDesktop}
            button={button}
          />
        )
      }
    }
  }

  return renderLayout()
}

export default ImageWithTextSection
