import React, { useMemo } from 'react'
import styled from 'styled-components'
import Swiper from 'react-id-swiper'
import 'swiper/css/swiper.css'

import Review from './Review'

const SwiperWrapper = styled.div`
  .swiper-pagination {
    position: static;
    width: calc(100% - 42px);
    margin: 21px auto 0;
    padding-top: 20px;
    border-top: 1px solid ${props => props.theme.colors.greyLight};
  }

  .swiper-pagination-bullet {
    width: 6px;
    height: 6px;
  }

  .swiper-pagination-bullet-active {
    background: ${props => props.theme.colors.primary};
  }
`

const Slide = styled.div``

const params = {
  autoHeight: true,
  pagination: {
    el: '.swiper-pagination',
    type: 'bullets',
  },
  preloadImages: false,
}

const ReviewsCarousel = ({ className, reviews }) => {
  const size = 12

  const memoizedReviews = useMemo(() => reviews.slice(0, size).map(r => r), [
    reviews,
  ])

  return (
    <SwiperWrapper className={className}>
      <Swiper {...params}>
        {memoizedReviews.map(item => {
          return (
            <Slide key={item.id}>
              <Review
                author={item.author}
                imagesFileName={item.imagesFileName}
                title={item.title}
                product_title={item.product_title}
                body={item.body}
                rating={item.rating}
                dateCreated={item.dateCreated}
                link={item.contentful_product_handle}
              />
            </Slide>
          )
        })}
      </Swiper>
    </SwiperWrapper>
  )
}

export default ReviewsCarousel
