import React from 'react'
import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'

import { desktopVw } from '../../styles/utils'
import theme from '../../styles/theme'

import ScaledImage from '../ScaledImage'
import Button from '../Button'
import { getLinkPath, imageSources } from '../../utils'

const StyledTextOverlap = styled.section`
  margin: ${props => props.theme.spacings.mobile}px 0 29px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: relative;
    display: flex;
    align-items: center;
    margin: ${desktopVw(64)} 0 ${desktopVw(111)};
  }
`

const ImageContainer = styled.div`
  padding: 0 ${props => props.theme.spacings.mobile}px 0;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0 0 0 ${desktopVw(121)};
  }
`

const ImageWrapper = styled.div`
  position: relative;
  box-sizing: border-box;
  margin: 0 0 18px;
  height: 460px;
  width: 100%;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-shrink: 0;
    margin: 0;
    height: ${desktopVw(783)};
    width: ${desktopVw(759)};
  }
`

const TextWrapper = styled.div`
  padding: 0 ${props => props.theme.spacings.mobile}px;

  h2,
  h3 {
    margin-bottom: 16px;
    font-size: ${props => (props.titleFontSize ? props.titleFontSize : 40)}px;
    line-height: 1.125;
  }

  p {
    margin-bottom: 32px;
    font-size: ${props =>
      props.paragraphFontSize ? props.paragraphFontSize : 14}px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: absolute;
    top: ${desktopVw(173)};
    right: ${desktopVw(245)};
    max-width: ${desktopVw(402)};
    padding: 0;

    h2,
    h3 {
      margin-bottom: ${desktopVw(23)};
      font-size: ${props =>
        props.titleFontSizeDesktop
          ? desktopVw(props.titleFontSizeDesktop)
          : desktopVw(80)};
      line-height: 1;
    }

    p {
      margin-bottom: ${desktopVw(37)};
      font-size: ${props =>
        props.paragraphFontSizeDesktop
          ? desktopVw(props.paragraphFontSizeDesktop)
          : desktopVw(20)};
      line-height: 1.25;
    }
  }
`

const StyledButton = styled(Button)`
  display: block;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: inline-block;
    font-size: ${desktopVw(18)};
    text-transform: uppercase;
    font-family: ${props => props.theme.fonts.primary};
    padding: ${desktopVw(25)} ${desktopVw(97)};
  }
`

const TextOverlap = ({
  image,
  imageDesktop,
  text,
  button,
  titleFontSize,
  titleFontSizeDesktop,
  paragraphFontSize,
  paragraphFontSizeDesktop,
}) => (
  <StyledTextOverlap>
    <ImageContainer>
      <ImageWrapper>
        <ScaledImage
          backgroundColor={theme.colors.greyLighter}
          fluid={imageSources(image, imageDesktop)}
          style={{
            position: 'absolute',
            left: '0',
            right: '0',
            bottom: '0',
            top: '0',
            zIndex: '-1',
          }}
          alt={image.description || image.title}
        />
      </ImageWrapper>
    </ImageContainer>
    <TextWrapper
      titleFontSize={titleFontSize}
      titleFontSizeDesktop={titleFontSizeDesktop}
      paragraphFontSize={paragraphFontSize}
      paragraphFontSizeDesktop={paragraphFontSizeDesktop}
    >
      {documentToReactComponents(text.json)}
      <StyledButton
        to={getLinkPath(button)}
        trackParams={{
          trackGA: true,
          trackInternal: true,
          category: 'home_page',
          action: 'click_layout_two_button',
          label: button.text,
          value: getLinkPath(button),
        }}
      >
        {button.text}
      </StyledButton>
    </TextWrapper>
  </StyledTextOverlap>
)

export default TextOverlap
