import React from 'react'
import styled from 'styled-components'
import dayjs from 'dayjs'
import { Link } from 'gatsby'

import { desktopVw } from '../styles/utils'
import { getPathName } from '../utils'

import Rating from './Rating'
import { getPathParams } from '../utils'

const StyledReview = styled.article`
  display: flex;
  align-items: flex-start;
  padding: 14px 21px 0;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(24)} ${desktopVw(91)} ${desktopVw(24)} ${desktopVw(11)};
  }
`

const Content = styled.div``

const Header = styled.header`
  margin-bottom: 15px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(8)};
  }
`

const Title = styled.h4`
  font-size: 15px;
  line-height: 20px;
  font-family: ${props => props.theme.fonts.primary};
  font-weight: 500;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(15)};
    line-height: ${desktopVw(20)};
  }
`

const Message = styled.p`
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
  width: 100%;
  margin-bottom: 13px;
  font-size: 13px;
  line-height: 16px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    -webkit-line-clamp: unset;
    font-size: ${desktopVw(15)};
    line-height: ${desktopVw(20)};
    margin-bottom: ${desktopVw(13)};
  }
`

const Footer = styled.footer`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-size: 10px;
  color: ${props => props.theme.colors.greyLight};
  text-transform: uppercase;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
    font-size: ${desktopVw(10)};
  }
`

const ProductNameContainer = styled.div`
  margin-bottom: 6px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: inline;
    margin: 0 1em 0 0;
  }
`

const ProductName = styled.span`
  line-height: 17px;
  text-decoration: underline;
`

const AuthorWrapper = styled.div`
  display: inline;
`

const Author = styled.cite`
  margin-right: 1em;
  font-style: normal;
  white-space: pre;
`

const StyledRating = styled(Rating)`
  margin-bottom: 8px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(8)};
  }
`

const Review = ({
  children,
  author,
  title,
  body,
  rating,
  dateCreated,
  product_title,
  link,
  tracker,
}) => {
  const formattedDate = dayjs(dateCreated).format('D MMM YYYY')
  return (
    <StyledReview>
      {children}
      <Content>
        <Header>
          <StyledRating rating={rating} />
          <Title>{title}</Title>
        </Header>
        <Message>{body}</Message>
        <Footer>
          <ProductNameContainer>
            {link ? (
              <Link
                to={
                  getPathName(link) +
                  (tracker ? getPathParams({ tracker }) : '')
                }
              >
                <ProductName>{product_title}</ProductName>
              </Link>
            ) : (
              <ProductName>{product_title}</ProductName>
            )}
          </ProductNameContainer>
          <AuthorWrapper>
            <Author>{author}</Author>
            <time dateTime={dateCreated}>{formattedDate}</time>
          </AuthorWrapper>
        </Footer>
      </Content>
    </StyledReview>
  )
}

export default Review
