import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../styles/utils'
import RichText from './RichText'

const StyledTextSection = styled.section`
  text-align: center;
  margin: 70px 0 49px;
  padding: 0 ${props => props.theme.spacings.mobile}px;

  h2,
  h3 {
    margin-bottom: 22px;
    font-family: ${props => props.theme.fonts.secondary};
    font-size: ${props => (props.titleFontSize ? props.titleFontSize : '55')}px;
    line-height: 0.91;
    font-weight: 500;
  }

  p {
    font-size: ${props =>
      props.paragraphFontSize ? props.paragraphFontSize : '14'}px;
    line-height: 1.5;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 0 ${desktopVw(115)};

    h2,
    h3 {
      margin: 0 auto ${desktopVw(30)};
      font-size: ${props =>
        props.titleFontSizeDesktop
          ? desktopVw(props.titleFontSizeDesktop)
          : desktopVw(120)};
      line-height: 0.916;
      max-width: ${desktopVw(1068)};
      white-space: pre-wrap;
    }

    p {
      margin: 0 auto;
      max-width: ${desktopVw(640)};
      font-size: ${props =>
        props.paragraphFontSizeDesktop
          ? desktopVw(props.paragraphFontSizeDesktop)
          : desktopVw(25)};
      line-height: 1.2;
    }
  }
`

const TextSection = ({
  className,
  json,
  titleFontSize,
  titleFontSizeDesktop,
  paragraphFontSize,
  paragraphFontSizeDesktop,
}) => (
  <StyledTextSection
    className={className}
    titleFontSize={titleFontSize}
    titleFontSizeDesktop={titleFontSizeDesktop}
    paragraphFontSize={paragraphFontSize}
    paragraphFontSizeDesktop={paragraphFontSizeDesktop}
  >
    <RichText json={json} />
  </StyledTextSection>
)

export default TextSection
