import React, { useState, useEffect, useCallback } from 'react'
import styled from 'styled-components'
import Image from 'gatsby-image'

import theme from '../styles/theme'
import { desktopVw } from '../styles/utils'

import { getLinkPath } from '../utils'
import TrackableLink from './TrackableLink'

const StyledCategoryThumbnail = styled(TrackableLink)`
  position: relative;
  display: flex;
  justify-content: center;
  padding: 20px;
  box-sizing: border-box;
  height: 246px;
  width: calc(50% - 5px);
  margin-bottom: 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0;
    padding: ${desktopVw(69.9)} 0;
    width: calc(1 / 3 * 100% - (1 / 3 * ${desktopVw(80)}));
    height: ${desktopVw(573)};
  }
`

const Title = styled.h3`
  font-size: 20px;
  align-self: flex-end;
  text-align: center;
  color: ${props => props.theme.colors.secondary};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(30)};
  }
`

const StyledImage = styled(Image)`
  transition: opacity 350ms ease-out;
`

const CategoryThumbnail = ({ className, images, link, timeoutDelay }) => {
  const [imageIndex, setImageIndex] = useState(0)
  const [intervalId, setIntervalId] = useState(null)

  useEffect(() => () => clearInterval(intervalId), [intervalId])

  const cycleImages = useCallback(
    delay => {
      if (images.length > 1) {
        setImageIndex(imageIndex + 1)
        const cycleImages = setInterval(
          () =>
            setImageIndex(prevImageIndex =>
              prevImageIndex < images.length - 1 ? prevImageIndex + 1 : 0,
            ),
          delay,
        )

        setIntervalId(cycleImages)
      }
    },
    [imageIndex, images.length],
  )

  // eslint-disable react-hooks/exhaustive-deps

  useEffect(() => {
    const mq = window.matchMedia(
      `(min-width: ${theme.breakpoints.desktopSmall}px)`,
    )

    if (mq.matches) return

    const timer = setTimeout(() => {
      cycleImages(3500)
    }, timeoutDelay)

    return () => clearTimeout(timer)
  }, [timeoutDelay])

  const handleMouseEnter = () => {
    cycleImages(1500)
  }

  const handleMouseLeave = () => {
    setImageIndex(0)
    clearInterval(intervalId)
  }

  return (
    <StyledCategoryThumbnail
      className={className}
      to={getLinkPath(link)}
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
      trackParams={{
        trackGA: true,
        trackInternal: true,
        category: 'home_page',
        action: 'click_shop_collection',
        label: link.text,
        value: getLinkPath(link),
      }}
    >
      {images.map(({ id, fluid, description, title }, i) => (
        <StyledImage
          key={id}
          fluid={fluid}
          style={{
            position: 'absolute',
            left: '0',
            right: '0',
            bottom: '0',
            top: '0',
            opacity: imageIndex === i ? '1' : '0',
            zIndex: imageIndex === i ? '-1' : '-2',
          }}
          alt={description || title}
        />
      ))}
      <Title>{link && link.text}</Title>
    </StyledCategoryThumbnail>
  )
}

export default CategoryThumbnail
