import React, { useState, useMemo } from 'react'
import styled from 'styled-components'

import { desktopVw } from '../styles/utils'

import Review from './Review'
import ReviewThumbnail from './ReviewThumbnail'
import ReviewModal from './ReviewModal'

const StyledReviewsList = styled.div`
  width: 50%;
  height: 100%;
  box-sizing: border-box;
  border-top: 1px solid ${props => props.theme.colors.greyLighter};
  border-bottom: 1px solid ${props => props.theme.colors.greyLighter};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0 ${desktopVw(69)} 0 ${desktopVw(80)};
  }
`

const List = styled.ul`
  overflow: hidden;
  overflow-y: scroll;
  height: calc(100% - ${desktopVw(79)});

  &::-webkit-scrollbar {
    display: none;
  }
`

const ListItem = styled.li`
  border-top: 1px solid ${props => props.theme.colors.greyLighter};

  &:first-child {
    border-top: none;
  }
`

const ListHeader = styled.div`
  background: ${props => props.theme.colors.secondary};
  border-bottom: 1px solid ${props => props.theme.colors.greyLighter};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    align-items: center;
    height: ${desktopVw(79)};
  }
`

const Keyword = styled.span`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(16)};
    line-height: ${desktopVw(45)};
    margin-right: ${desktopVw(12)};
  }
`

const ReviewAmount = styled.span`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
    text-transform: uppercase;
  }
`

const ReviewThumbnailWrapper = styled.div`
  margin: 35px 21px 0 0;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(35)} ${desktopVw(21)} 0 0;
  }
`

const ReviewCount = styled.span`
  font-weight: 500;
  font-size: ${desktopVw(20)};
  line-height: ${desktopVw(45)};
  display: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: inline-block;
  }
`

const ReviewsList = ({ className, reviews, tracker }) => {
  const [modalOpen, setModalOpen] = useState('')

  const reviewsLength = reviews.length

  const onPrevClick = i => {
    if (i > 0) {
      setModalOpen(reviews[i - 1].id)
    }
  }

  const onNextClick = i => {
    if (i < reviewsLength - 1) {
      setModalOpen(reviews[i + 1].id)
    }
  }

  return (
    <StyledReviewsList className={className}>
      <ListHeader>
        <div>
          <Keyword>
            <ReviewCount>10,000+</ReviewCount> Five Star Reviews
          </Keyword>
        </div>
      </ListHeader>
      <List>
        {reviews.map((pr, i) => (
          <ListItem key={pr.id}>
            <Review
              {...pr}
              modalOpen={modalOpen}
              openModal={() => setModalOpen(pr.id)}
              reviewsLength={reviewsLength}
              closeModal={() => setModalOpen(null)}
              activeModal={i}
              onPrevClick={() => onPrevClick(i)}
              onNextClick={() => onNextClick(i)}
              link={pr.contentful_product_handle}
              tracker={tracker}
            >
              <ReviewThumbnailWrapper>
                <ReviewThumbnail
                  reviewUserPhotos={pr.imagesFileName}
                  openModal={() => setModalOpen(pr.id)}
                >
                  {pr.imagesFileName && (
                    <ReviewModal
                      open={modalOpen === pr.id}
                      reviewsLength={reviewsLength}
                      closeModal={() => setModalOpen(null)}
                      activeModal={i}
                      onPrevClick={() => onPrevClick(i)}
                      onNextClick={() => onNextClick(i)}
                      {...pr}
                    />
                  )}
                </ReviewThumbnail>
              </ReviewThumbnailWrapper>
            </Review>
          </ListItem>
        ))}
      </List>
    </StyledReviewsList>
  )
}

export default ReviewsList
