import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'
import theme from '../../styles/theme'
import { getLinkPath, imageSources } from '../../utils'

import ScaledImage from '../ScaledImage'
import Button from '../Button'
import RichText from '../RichText'

const StyledTile = styled.section`
  margin: 0 0 40px;
  position: relative;
  box-sizing: border-box;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    margin: 0;
    flex-direction: ${props =>
      props.reverse ? 'row-reverse' : 'row'} !important;
  }
`

const SubSectionImageWrapper = styled.div`
  position: relative;
  height: 468px;
  margin: 0 0 48px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0;
    flex: 1 0 50%;
    height: 100vh;
  }
`

const SubSectionTextWrapper = styled.div`
  padding: 0 30px;
  white-space: pre-wrap;

  h1,
  h2,
  h3 {
    font-size: ${props => (props.titleFontSize ? props.titleFontSize : '49')}px;
    line-height: 0.88;
    margin-bottom: 10px;
    font-family: ${props => props.theme.fonts.secondary};
    text-align: left;
  }

  h4 {
    font-family: ${props => props.theme.fonts.primary};
    font-size: 16px;
    line-height: 20px;
    font-weight: 400;
  }

  p {
    margin-bottom: 38px;
    font-size: ${props =>
      props.paragraphFontSize ? props.paragraphFontSize : '12'}px;
    line-height: 1.42;
  }

  ol {
    font-size: 16px;
    p {
      font-size: 16px;
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    align-self: center;
    flex: 1;
    flex-basis: ${props => (props.showBiggerImage ? 45 : 50)}%;
    padding: 0 ${desktopVw(110)} 0 ${desktopVw(80)};

    h1,
    h2,
    h3 {
      margin-bottom: ${desktopVw(40)};
      font-size: ${props =>
        props.titleFontSizeDesktop
          ? desktopVw(props.titleFontSizeDesktop)
          : desktopVw(60)};
      line-height: 1.08;
    }

    h4 {
      font-size: ${desktopVw(25)};
      line-height: ${desktopVw(30)};
    }

    p {
      margin-bottom: ${desktopVw(40)};
      font-size: ${props =>
        props.paragraphFontSizeDesktop
          ? desktopVw(props.paragraphFontSizeDesktop)
          : desktopVw(20)};
      line-height: 1.25;
    }

    ol {
      font-size: ${desktopVw(25)};
      p {
        font-size: ${desktopVw(25)};
      }
    }
  }
`

const StyledButton = styled(Button)`
  display: block;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: inline-block;
    align-self: flex-start;
    text-transform: uppercase;
    padding: ${desktopVw(25)} ${desktopVw(97)};
    font-size: ${desktopVw(18)};
  }
`

const RichTextMobile = styled(RichText)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const RichTextDesktop = styled(RichText)`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
  }
`

const Tile = ({
  className,
  image,
  imageDesktop,
  titleFontSize,
  titleFontSizeDesktop,
  paragraphFontSize,
  paragraphFontSizeDesktop,
  text,
  textMobile,
  reverse,
  showImageMargin,
  showBiggerImage,
  button,
  children,
}) => {
  return (
    <StyledTile reverse={reverse} className={`${className} Tile`}>
      <SubSectionImageWrapper
        className='tile__image-wrapper'
        showImageMargin={showImageMargin}
        showBiggerImage={showBiggerImage}
      >
        <ScaledImage
          backgroundColor={theme.colors.greyLighter}
          fluid={imageSources(image, imageDesktop)}
          style={{
            position: 'absolute',
            left: '0',
            right: '0',
            bottom: '0',
            top: '0',
            zIndex: '-1',
          }}
          alt={image.description || image.title}
        />
      </SubSectionImageWrapper>
      <SubSectionTextWrapper
        className='tile__text-wrapper'
        titleFontSize={titleFontSize}
        titleFontSizeDesktop={titleFontSizeDesktop}
        paragraphFontSize={paragraphFontSize}
        paragraphFontSizeDesktop={paragraphFontSizeDesktop}
        showBiggerImage={showBiggerImage}
      >
        <RichTextDesktop className='tile__rich-text-desktop' json={text.json} />
        <RichTextMobile
          className='tile__rich-text-mobile'
          json={textMobile ? textMobile.json : text.json}
        />
        {button && (
          <StyledButton
            className='tile__button-wrapper'
            to={getLinkPath(button)}
            trackParams={{
              trackGA: true,
              trackInternal: true,
              category: 'home_page',
              action: 'click_layout_one_button',
              label: button.text,
              value: getLinkPath(button),
            }}
          >
            {button.text}
          </StyledButton>
        )}
        {children}
      </SubSectionTextWrapper>
    </StyledTile>
  )
}

export default Tile
